<template>
  <div id='charticulatorContainer'/>
</template>

<script>
import {CharticulatorWrap} from './charticulator.js'

export default {
  name:'charticulator',
  mounted(){
    let charticulatorWrap=new CharticulatorWrap();
    charticulatorWrap.init('charticulatorContainer').then(()=>{
      this.$store.commit("initCharticulator",{charticulatorWrap});
      console.log(charticulatorWrap)
    });
    
  }
}
</script>

<style lang="sass" scoped>
div
  visibility: hidden
</style>