<template>
  <svg id="miniMap" @click="focus">
    <!-- <line v-if="keyFrameTime.length>=2" :x1="60" :x2="60" 
    stroke-dasharray="2, 2"
      :y1="keyFrameTime[0]*screenHeightDividedBySvgHeigh+2" 
      :y2="keyFrameTime[1]*screenHeightDividedBySvgHeigh+2" /> -->
    <line v-if="keyFrameTime.length>=2" :x1="58" :x2="58" 
      :y1="keyFrameTime[0]*screenHeightDividedBySvgHeigh+2" 
      :y2="keyFrameTime[1]*screenHeightDividedBySvgHeigh+2" />
      <line v-if="keyFrameTime.length>=2" :x1="62" :x2="62" 
      :y1="keyFrameTime[0]*screenHeightDividedBySvgHeigh+2" 
      :y2="keyFrameTime[1]*screenHeightDividedBySvgHeigh+2" />
      <line v-for="(time,index) in keyFrameTimeMOne"
    :key="`cLine${index}`"
    stroke-dasharray="2, 2"
    :x1="(keyFrameTimeMOne.length===1)?60:60+20*(index-1)"
    :x2="(keyFrameTimeMOne.length===1)?60:60+20*(index-1)"
    :y1="(index===0&&keyFrameTimeMOne.length>1)?keyFrameTime[index+1]*screenHeightDividedBySvgHeigh+5:keyFrameTime[index]*screenHeightDividedBySvgHeigh+5" 
    :y2="(index===keyFrameTimeMOne.length-1)?1000:keyFrameTime[index+1]*screenHeightDividedBySvgHeigh+5"/>
    <!-- <line/> 第一个帧没有 第二帧加了legend然后一直往后 第三帧是label 第四帧label消失  新的Lable -->
    <g v-if="keyFrameTime.length===2">
      <!-- <circle v-for="" :key="`cir${index}`"/> -->
      <line/>
    </g>
    <g v-if="keyFrameTime.length===3">
      
      <line v-for="(l,index) in keyFrameThree.line" :key="`nonL${index}`"
      :x1="l.x1"
      :x2="l.x2"
      :y1="keyFrameTime[l.y1]*screenHeightDividedBySvgHeigh"
      :y2="(l.y2===1000)?1000:keyFrameTime[l.y2]*screenHeightDividedBySvgHeigh"
      stroke-dasharray="2, 2"
      />
      <circle v-for="(c,index) in keyFrameThree.circle" :key="`nonC${index}`"
      :cx="c.cx"
      :cy="(c.cy===1000)?1000:keyFrameTime[c.cy]*screenHeightDividedBySvgHeigh+c.r"
      :r="c.r"
      fill='#F8B62D'
      />
    </g>
    <g v-if="keyFrameTime.length===4">
      <line v-for="(l,index) in keyFrameFour.line" :key="`nonL${index}`"
      :x1="l.x1"
      :x2="l.x2"
      :y1="keyFrameTime[l.y1]*screenHeightDividedBySvgHeigh"
      :y2="(l.y2===1000)?1000:keyFrameTime[l.y2]*screenHeightDividedBySvgHeigh"
      stroke-dasharray="2, 2"
      />
      <circle v-for="(c,index) in keyFrameFour.circle" :key="`nonC${index}`"
      :cx="c.cx"
      :cy="(c.cy===1000)?1000:keyFrameTime[c.cy]*screenHeightDividedBySvgHeigh+c.r"
      :r="c.r"
      fill='#F8B62D'
      />
    </g>
    <g v-if="keyFrameTime.length===5">
      <line v-for="(l,index) in keyFrameFive.line" :key="`nonL${index}`"
      :x1="l.x1"
      :x2="l.x2"
      :y1="keyFrameTime[l.y1]*screenHeightDividedBySvgHeigh"
      :y2="(l.y2===1000)?1000:keyFrameTime[l.y2]*screenHeightDividedBySvgHeigh"
      stroke-dasharray="2, 2"
      />
      <circle v-for="(c,index) in keyFrameFive.circle" :key="`nonC${index}`"
      :cx="c.cx"
      :cy="(c.cy===1000)?1000:keyFrameTime[c.cy]*screenHeightDividedBySvgHeigh+c.r"
      :r="c.r"
      fill='#F8B62D'
      />
    </g>
    <!-- <MiniLink v-for="(link,index) in links" 
    :key="`link${index}`" :beginNode="link.beginNode"
    :endNode="link.endNode" :type="link.type"/> -->
    <MiniNode v-for="(nodeProps,index) in nodes" 
    :key="`node${index}`" :node="nodeProps"/>
    <rect :id="`trigger${index}`" 
    v-for="(trigger,index) in triggerArr" 
    :key="`trigger${index}`" 
    :x="60+20*(index)" 
    :y="trigger.time*screenHeightDividedBySvgHeigh" width="20" height="10" 
    @click.stop="clickTrigger(trigger)" @mousedown="downTrigger(index)" @mousemove="moveTrigger(index, $event)"  @mouseup="upTrigger"/>
    <!-- <rect v-for="(roll,index) in rollArr" :key="`roll${index}`" 
    x="100" 
    :y="roll.PreviousTime*screenHeightDividedBySvgHeigh" 
    width="3" 
    :height="(roll.time-roll.PreviousTime)*screenHeightDividedBySvgHeigh"/> -->
    <line x1="0" :y1="focusTime" x2="330"  :y2="focusTime" />
    <circle v-for="(time,index) in keyFrameTime" :key="`chart${index}`"
    tabindex="0" :cx="(index===0 || index===1)?60:60+20*(index-2)" :cy="time*screenHeightDividedBySvgHeigh+5" r="5" fill="#F8B62D"/>
    
  </svg>
</template>

<script>
import MiniNode from './MiniNode.vue'
import MiniLink from './MiniLink.vue'
import * as util from '../../util/util'

export default {
  components:{
    MiniNode,
    MiniLink
  },
  name:'miniSVG',
  data(){
    return {
      backgroundNodeArr:[],
      foreignObjectNodeArr:[],
      selectedTrigger:'',
      keyFrameThree:{circle:[{cx:150,r:5,color:'pink',cy:2}],
        line:[{x1:150,y2:1000,x2:150,dash:"2, 2",y1:2}]
        },
      keyFrameFour:{circle:[{cx:170,r:5,color:'pink',cy:2},
                            {cx:190,r:5,color:'pink',cy:3}],
        line:[{x1:170,y2:1000,x2:170,dash:"2, 2",y1:2},
              {x1:190,y2:1000,x2:190,dash:"2, 2",y1:3}]
        },
      keyFrameFive:{circle:[{cx:190,r:5,color:'pink',cy:2},
                            {cx:210,r:5,color:'pink',cy:3},
                            {cx:210,r:5,color:'pink',cy:4},
                            {cx:230,r:5,color:'pink',cy:4}],
        line:[{x1:190,y2:1000,x2:190,dash:"2, 2",y1:2},
              {x1:210,y2:4,x2:210,dash:"2, 2",y1:3},
              {x1:230,y2:1000,x2:230,dash:"2, 2",y1:4}]
        },
    }
  },
  computed:{
    screenHeightDividedBySvgHeigh(){
      return 827/this.$store.state.svgHeightMax; //minimap is 827 px;
    },
    nodes(){
      let arr=[...this.backgroundNodeArr]//,...this.foreignObjectNodeArr];
      return arr;
    },
    links(){
      return [];
      // [{
      //   beginNode:{
      //     position:{
      //       x:20,
      //       y:20,
      //     },
      //     type:'',
      //     dom:null,
      //   },
      //   endNode:{
      //     position:{
      //       x:40,
      //       y:40,
      //     },
      //     type:'',
      //     dom:null,
      //   },
      //   type:'static'
      // }]
    },
    focusTime(){
      return this.$store.state.focusTime*this.screenHeightDividedBySvgHeigh;
    },
    triggerArr(){
      return this.$store.state.triggerArr;
    },
    rollArr(){
      console.log('rollArr',this.$store.state.rollArr);
      return this.$store.state.rollArr;
    },
    loadingFile(){
      console.log(222)
      return this.$store.state.loadingFile;
    },
    keyFrameTime(){
      return this.$store.state.keyFrameTime;
    },
    keyFrameTimeMOne(){
      return this.$store.state.keyFrameTime;//.length>1)?this.$store.state.keyFrameTime:this.$store.state.keyFrameTime.slice(0,-1);
    },
    editing(){
      return this.$store.state.editing
    }
  },
  watch:{
    loadingFile(){
      let background;
      if (this.editing)
        background=Array.from(document.querySelectorAll('#topClone foreignObject'))
      if (!this.editing)
        background=Array.from(document.querySelectorAll('#topCanvas foreignObject'))
      this.backgroundNodeArr=background.map(x=>{
        return {
          dom:x,
          type:'background',
          position:{
            'x':20,
            'y':parseFloat(x.getAttribute('y'))*this.screenHeightDividedBySvgHeigh,
          }
        }
      })
      const keyFrames=this.$store.state.keyFrame;
      this.foreignObjectNodeArr=[];
      Object.keys(keyFrames).forEach(time=>{
        
        const keyFrame=keyFrames[time]
        if (!keyFrame) return;
        const foreignObjectArr=keyFrame.filter(shape=>shape.tagName==='foreignObject');
        const newforeignObjectNodeArr=foreignObjectArr.map((x,index)=>{
          console.log('id',x.id)
          console.log('pos',{
              x:40+index*20,
              y:time*this.screenHeightDividedBySvgHeigh,
            })
          let xPos=(parseInt(x.id.slice(1))-994)*20 //随便减一个id
          return {
            dom:(!x.id)?null:document.querySelector(x.id),
            type:'foreignObject',
            position:{
              x:xPos,
              y:time*this.screenHeightDividedBySvgHeigh,
            }
          }
        })
        this.foreignObjectNodeArr=[...this.foreignObjectNodeArr,...newforeignObjectNodeArr];
      })
    },

  },
  methods:{
    downTrigger(index){
      this.selectedTrigger=`#trigger${index}`
    },
    upTrigger(e){
      this.selectedTrigger=''
    },
    moveTrigger(index,e){
      console.log(e,'move')
      if (this.selectedTrigger!==''){
        const dom=document.querySelector(`#trigger${index}`);
        const w=parseFloat(dom.getAttribute('width'));
        dom.setAttribute('width',w+e.movementX);
        this.$store.commit('updateAnimationTime',{animationTime:(w+e.movementX)*10})
      }
      
    },
    clickTrigger(trigger){
      console.log(trigger);
      util.playTrigger(trigger);
    },
    focus(e){
      const oldTime=this.$store.state.focusTime;
      this.$store.commit('focus',{focusTime:e.layerY,oldTime})
    }
  }

}
</script>

<style lang="sass" scoped>
#miniMap
  position: absolute
  width: 329px
  top: 160px
  left: 1183px
  height: 827px
  border-left: 0.5px solid #888

line
  stroke: black
  stroke-width: 2
  transition: all 0.3s
</style>