<template>
<img @click="fakeDownload" style="position:absolute;top:-5px;left:-50px;" src="../../icons/svg/ttbar.svg" />
  <div id="toolBar" @mousewheel="scroll">
    <!-- <img src="../../icons/svg/save.svg" @click="newButton" /> -->
    <!-- <img src="../../icons/svg/edit.svg" @click="editButton" :class="(selectedEditing)?'selecetedImg':null" /> -->
    <!-- <img src="../../icons/svg/new.svg" @click="saveButton" :class="(selectState==='addText')?'selecetedImg':null"/> -->
    <!-- <img src="../../icons/svg/preview.svg" @click="previewButton" :class="(selectedPacking)?'selecetedImg':null"/> -->
    <el-dropdown class="charticulator" >
      <img src="../../icons/svg/Charticulator.svg" />
    <!-- <span class="el-dropdown-link">
      Charticulator
      <el-icon class="el-icon--right">
        <arrow-down />
      </el-icon>
    </span> -->
    <template #dropdown>
      <el-dropdown-menu>
        <!-- <el-dropdown-item @click="CCircle">circle</el-dropdown-item>
        <el-dropdown-item @click="CRect">rect</el-dropdown-item> -->
        <el-dropdown-item @click="CChart">Chart</el-dropdown-item>
        <el-dropdown-item @click="unbind" divided>Guide Y</el-dropdown-item>
        <el-dropdown-item @click="CRect">Guide X</el-dropdown-item>
        <el-dropdown-item @click="CRect">Guide polar</el-dropdown-item>
        <el-dropdown-item @click="CRect" divided>2D Plot</el-dropdown-item>
        <el-dropdown-item @click="CRect">Line Plot</el-dropdown-item>
        <el-dropdown-item @click="CRect" divided>Icon Scafflods</el-dropdown-item>
        <el-dropdown-item @click="CRect">Custom Scafflods</el-dropdown-item>
      </el-dropdown-menu>
    </template>
  </el-dropdown>   

    <el-dropdown class="system">
    <img src="../../icons/svg/System.svg" />
    <template #dropdown>
      <el-dropdown-menu>
        <!-- <el-dropdown-item @click="saveRoll">save as roll</el-dropdown-item> -->
        <!-- <el-dropdown-item @click="saveTrigger">save as trigger</el-dropdown-item> -->
        <!-- <el-dropdown-item @click="bigger">bigger</el-dropdown-item> -->
        <!-- <el-dropdown-item @click="smaller">smaller</el-dropdown-item> -->
        <!-- <el-dropdown-item  @click="saveFile" divided>save </el-dropdown-item> -->
        <el-dropdown-item @click="loadFile">forceGraph</el-dropdown-item>
        <el-dropdown-item @click="circlePacking">circlePacking</el-dropdown-item>
        <!-- <el-dropdown-item @click="scrolly">scrolly!</el-dropdown-item> -->
        <el-dropdown-item @click="preview">anyShapePacking</el-dropdown-item>
        
      </el-dropdown-menu>
    </template>
  </el-dropdown>   
  <el-dropdown class="shape" :hide-on-click="false">
    <img src="../../icons/svg/shape.svg" />
    <template #dropdown>
      <el-dropdown-menu>
        <el-dropdown-item @click="addLine">line</el-dropdown-item>
        <el-dropdown-item @click="addingRect">rect</el-dropdown-item>
        <el-dropdown-item @click="addingCircle">circle</el-dropdown-item>
        
        <el-dropdown-item @click="saveButton">text</el-dropdown-item>
        
        <el-dropdown-item @click="saveButton">image</el-dropdown-item>
        
        <el-dropdown-item @click="saveButton">video</el-dropdown-item>
        <el-dropdown-item divided>color</el-dropdown-item>
        <el-dropdown-item  ><el-color-picker v-model="fill" :predefine="predefineColors" show-alpha/></el-dropdown-item>
        <el-dropdown-item divided>opacity</el-dropdown-item>
        <el-dropdown-item  ><el-input-number controls-position="right" size="small" v-model="opacity" :min="0" :max="1" :step="0.1"/></el-dropdown-item>
        <!-- <el-dropdown-item @click="bigger">bigger</el-dropdown-item>
        <el-dropdown-item @click="smaller">smaller</el-dropdown-item>
        <el-dropdown-item  @click="saveFile" divided>save </el-dropdown-item>
        <el-dropdown-item @click="loadFile">load </el-dropdown-item>
        <el-dropdown-item @click="circlePacking">circlePacking </el-dropdown-item>
        <el-dropdown-item @click="scrolly">scrolly!</el-dropdown-item>
        <el-dropdown-item @click="preview">preview</el-dropdown-item> -->
        <el-dropdown-item @click="border" divided>addBorder</el-dropdown-item>
      </el-dropdown-menu>
    </template>
  </el-dropdown>  
  <img src="../../icons/svg/active.svg" @click="saveTrigger" style="transform:translate(-200px,5px)"/>
  <img src="../../icons/svg/passive.svg" @click="saveRoll" style="transform:translate(-270px,5px)"/>
    <!-- <button @click="unbind">unbind</button>
    <button @click="addLine">line</button>
    <button @click="addingRect">rect</button>-->
    <!-- <button @click="border">border</button> -->
    
     <!-- <el-input-number v-model="opacity" :min="0" :max="1" :step="0.1"/>  -->
    <el-input v-model="uploadJson" style="position:absolute;width:100px"/>
  </div>
  <div id="textEditorToolBar"/>
  <div id='charticulatorToolBar' style="display:none"/>
</template>

<script>
import * as d3 from 'd3'
import {dfsCircles,bundleDoms,polyPacking,circlePacking} from '../../util/util'
import '@wangeditor/editor/dist/css/style.css'
import {ref} from 'vue'

export default {
  name:'toolBar',
  data(){
    return{
      opacity:1,
      uploadJson:'{}',
      fill:ref('rgba(0,0,0,0.5)'),
      predefineColors : ref([
        '#ff4500',
        '#ff8c00',
        '#ffd700',
        '#90ee90',
        '#00ced1',
        '#1e90ff',
        '#c71585',
        'rgba(255, 69, 0, 0.68)',
        'rgb(255, 120, 0)',
        'hsv(51, 100, 98)',
        'hsva(120, 40, 94, 0.5)',
        'hsl(181, 100%, 37%)',
        'hsla(209, 100%, 56%, 0.73)',
        '#c7158577',
        'rgba(22, 141, 217, 1)',
        'rgb(189, 189, 189)'
      ])
    }
  },
  computed:{
    selectedEditing(){
      return this.$store.state.editing;
    },
    selectState(){
      return this.$store.state.selectState;
    },
    initCharticulator(){
      return this.$store.state.initCharticulator;
    },
    selectedPacking(){
      return this.$store.state.packing;
    }
  },
  mounted(){
    // const editorToolbar=createToolbar({
    //   editor:,
    //   selector:'#textEditorToolBar',
    //   mode:'default',
    // })
    // new E('#textEditorToolBar');
    // this.$store.commit('createEditor',(editor));
    // editor.config.onchange=(newTextHTML)=>{
    //   this.$store.commit('newTextHTML',{newTextHTML});
    //   this.$store.commit('updateKeyFrameEdited');
    // }
    // editor.config.uploadImgShowBase64 = true;
    // editor.create();
  },
  watch:{
    fill(val){
      console.log(val)
      this.$store.commit('updataFill',{fill:val});
    },
    selectedEditing(val){
      if (!val){
        const circles=dfsCircles(document.querySelector('#mainCanvas'));
        const doms=circles;
        this.$store.commit('updateDoms',{doms})
        const that=this
        circles.forEach((circle,id) => {
          d3.select(circle)
          .on('click',null)
          .on('mouseover',()=>that.$store.commit('selectCircle',{id}));
        });
      }
    },
    initCharticulator(init){
      if (init){
      const charticulatorToolBar=document.querySelector('.charticulator__toolbar-horizontal');
      document.querySelector('#charticulatorToolBar').appendChild(charticulatorToolBar);
      document.querySelector('#mainCanvas').children[0].setAttribute("transform","translate(280,180) scale(0.6)");
      }
    },
    opacity(val){
      this.$store.commit('changeOpacity',{'opacity':val});
    }
  },
  methods:{
    fakeDownload(){
      let json=JSON.stringify({});
      json = [json];
      var blob1 = new Blob(json, { type: "text/plain;charset=utf-8" });
      var url = window.URL || window.webkitURL;
      link = url.createObjectURL(blob1);
      var a = document.createElement("a");
      a.download = "scrolly.html";
      a.href = link;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
    },
    CChart(){
      document.querySelector('.charticulator-title__button').click();
      // document.querySelector('.root-149').click();
    },
    addingRect(){
      document.querySelector(`[data-icon-name='RectangleShape']`).click();
      this.$store.commit('addingRect');
    },
    addingCircle(){
      document.querySelector(`[data-icon-name='Shapes']`).click();
    },
    border(){
      this.$store.commit('addBorder');
    },
    addLine(){
      document.querySelector(`[data-icon-name='Line']`).click();
      this.$store.commit('addingLine');
    },
    preview(){
      
    },
    unbind(){
      this.$store.commit('unbind');
    },
    scroll(e){
      //focusTime存的是真实距离
      //focus传递的需要是svg上的距离
      const focusTime=Math.min(Math.max(1,this.$store.state.focusTime+(e.deltaY/4)),this.$store.state.svgHeightMax)
      console.log(this.$store.state.focusTime,focusTime,e.deltaY);
      this.$store.commit('focus',{
        oldTime:this.$store.state.focusTime,
        focusTime,
        type:'scrolly'
      });
    },
    scrolly(){
      this.$store.commit('beginScrolly');
    },
    circlePacking(){
      circlePacking(null,this.$store.state.selectedDoms)
    },
    saveFile(){
      this.$store.commit('saveFile');
    },
    loadFile(){
      this.$store.commit('loadFile',{'uploadJson':this.uploadJson})
    },
    saveRoll(){
      this.$store.commit('saveAsRoll');
    },
    saveTrigger(){
      this.$store.commit('saveAsTrigger');
    },
    clickCircle(id){
      console.log(id)
    },
    newButton(){
      console.log(this.$store.state);
    },
    editButton(){
      this.$store.commit('clickEditing');
    },
    saveButton(){
      console.log(this.$store.state);
      if (this.selectState!=='addText')
        this.$store.commit('updateSelectState',{selectState:'addText'})
      else
        this.$store.commit('updateSelectState',{selectState:'normal'})
    },
    previewButton(){
      this.$store.commit('clickPacking');
      // this.$store.commit('updateDrawPackingState');
      // polyPacking([],this.$store.state.selectedDoms);
    }
  }
}
</script>

<style lang="sass">


#toolBar
  z-index: 99
  position: absolute
  width: 1512px
  height: 74.1
  left: 0px
  top: 74.1px
  background: #15499E
  box-sizing: border-box



img
  margin: 5px 50px
  cursor: pointer

.selecetedImg
  border: 1px solid #FFF
  border-radius: 10px

#textEditorToolBar
  position: absolute
  width: 1536px
  height: 100px
  left: 0px
  bottom: 50px

#charticulatorToolBar
  position: absolute
  width: 1512px
  height: 20px
  left: 0px
  top: 67px
  z-index: 99

.charticulator
  transform: translate(-20px, 23px)

.system
  transform: translate(-80px, 23px)

.shape
  transform: translate(-150px, 23px)
</style>

