<template>
  <div id='glyphEditor' style="overflow:scroll"/>
</template>

<script>
export default {
  name:'glyphEditor',
  data(){
    return {

    }
  },
  computed:{
    initCharticulator(){
      return this.$store.state.initCharticulator;
    }
  },
  watch:{
    initCharticulator(init){
      if (init){
      const charticulatorDataSet=document.querySelectorAll('.minimizable-panel-view')[0];
      document.querySelector('#glyphEditor').appendChild(charticulatorDataSet);
      const charticulator=document.querySelector('.charticulator__application');
      document.querySelector('#app')
      .appendChild(charticulator.lastChild)//toolbar
      .appendChild(charticulator.lastChild)//anotation
      }
    }
  },
}
</script>

<style lang="sass" scoped>
div 
  position: absolute
  width: 250px
  height: 833.63px
  left: 200px
  top: 160px
  background: white
  border-left: solid 0.5px #888

</style>
