<template>
  <g v-if="type==='static'">
    <line :x1="position.x1" :y1="position.y1"
      :x2="position.x2" :y2="position.y2"/>
  </g>
  <g v-else-if="type==='dynamic'">
    <line :x1="position.x1-3" :y1="position.y1"
      :x2="position.x-1" :y2="position.y2"/>
    <line :x1="position.x1+3" :y1="position.y1"
      :x2="position.x2+3" :y2="position.y2"/>
  </g>
</template>

<script>
export default {
  name:'miniLink',
  props:[
    'beginNode',
    'endNode',
    'type',
  ],
  computed:{
    position(){
      if (!this.beginNode)
        return{
          x1:this.endNode.position.x,
          y1:0,
          x2:this.endNode.position.x,
          y2:this.endNode.position.y,
        }
      if (!this.endNode)
        return{
          x1:this.beginNode.position.x,
          y1:this.beginNode.position.y,
          x2:this.beginNode.position.x,
          y2:1000,
        }
      return{
        x1:this.beginNode.position.x,
        y1:this.beginNode.position.y,
        x2:this.endNode.position.x,
        y2:this.endNode.position.y,
      }
    }
  }
}
</script>

<style lang="sass" scoped>

line
  stroke: black
  stroke-width: 2



</style>