<template>
  <div >
    <Charticulator v-show="false"/>
    <img @click="dMode" class="documentTag" src="../../icons/svg/document.svg" />
    <img @click="vMode" class="viewportTag" src="../../icons/svg/viewport.svg" />
    <div id="svgdiv" tabindex="0">
      <svg 
      id="svgCanvas"
      :style="(editing)?`height:369.64px`:`height:${svgHeightNonEditing}`"
      @mousewheel="scrollSvg"
      :transform="(editing)?`translate(0,0)`:`translate(0,${-scrollTop})`"
      @click.exact.stop="selectDom"
      @keydown.shift="goodLine()" @keyup.shift="badLine()"
      @click.ctrl.exact="selectMore"
      @keyup.delete.exact.stop="deleteDom()" tabindex="0" :class="(editing)?'editingCanvas':'notEditingCanvas'" @mousedown.self="selectBeginEvent" @mouseup="selectEndEvent" @mousemove="selectMoveEvent" @mouseleave="selectEndEvent">
        <path :d="pathString" stroke="black" transform="translate(450,350)" stroke-width="5"/>
        
        <g id="topCanvas"/>
        <g id="mainCanvas"/>
        <g id="background" transform="translate(0,0)"/>
        <path id='packingCurve' :d="packingPath" v-if="packing"/>
        <rect id="selectBox" v-if="select" :x="selectBox.x" :y="selectBox.y"
        :width="selectBox.width" :height="selectBox.height"/>
        <line id="addingLine" v-if="addingLine" :x1="lineBegin[0]" :y1="lineBegin[1]"
        :x2="lineEnd[0]" :y2="lineEnd[1]" 
        style="stroke:rgb(150,150,150);stroke-width:2"/>
        <rect id="addingRect" v-if="adding" :x="selectBox.x" :y="selectBox.y"
        :width="selectBox.width" :height="selectBox.height" style="fill:rgba(85, 242, 232, 0.5)"/>
      </svg>
    </div>
    <DataSet/>
    <GlyphEditor/>
    <DataFilter/>
  </div>
</template>

<script>
import Charticulator from './charticulator/Charticulator.vue';
import * as d3 from 'd3';
import {checkSelectedDoms,addTextDom,dfsCircles,dfsAllNodes,polyPacking} from '../../util/util';
import DataSet from './dataset/DataSet.vue';
import GlyphEditor from './glyphEditor/GlyphEditor.vue';
import DataFilter from './dataFilter/DataFilter.vue';

export default {
  components: { Charticulator,DataSet,GlyphEditor,DataFilter },
  name:'body',
  data(){
    return {
      pathString:'',
      select:false,
      selectBegin:[0,0],
      selectEnd:[0,0],
      scrollTop:0,
      svgHeightMax:827,
      packingArr:[],
      packingPath:'',
      lineBegin:[],
      lineEnd:[],
      shiftLine:false,
      adding:false,
    }
  },
  computed:{
    addingRect(){
      console.log(this.$store.state.addingRect);
      return this.$store.state.addingRect;
    },
    addingLine(){
      return this.$store.state.addingLine;
    },
    packing(){
      return this.$store.state.packing;
    },
    svgHeightNonEditing(){
      return this.svgHeightMax+'px'
    },
    selectedDoms(){
      return this.$store.state.selectedDoms;
    },
    allDoms(){
      return this.$store.state.allDoms;
    },
    editing(){
      return this.$store.state.editing;
    },
    selectedNodePath(){
      return this.$store.state.selectedNodePath;
    },
    selectBox(){
      return {
        x:Math.min(this.selectBegin[0],this.selectEnd[0]),
        y:Math.min(this.selectBegin[1],this.selectEnd[1]),
        width:Math.abs(this.selectBegin[0]-this.selectEnd[0]),
        height:Math.abs(this.selectBegin[1]-this.selectEnd[1]),
      }
    },
    selectState(){
      return this.$store.state.selectState;
    }
  },
  watch:{
    selectedNodePath(val){
      // let pathString=`M ${val[0].cx} ${val[0].cy}`;
      // for (let i=1;i<val.length;i+=1){
      //   pathString+=`L ${val[1].cx} ${val[1].cy}`;
      // }
      // this.pathString=pathString;
    },
    editing(val){
      if (val){
        document.querySelector('#mainCanvas').children[0].setAttribute("transform","translate(280,180) scale(0.6)");
        document.querySelector('#topCanvas').setAttribute("transform","translate(0,0)");
        document.querySelector('#background').setAttribute("transform","translate(0,0)");
        // const staticDocument=dfsCircles(document.querySelector('#svgCanvas'));
        const mainClone=document.querySelector('#mainCanvas').cloneNode(true);
        mainClone.setAttribute('id','mainClone');
        const topClone=document.querySelector('#topCanvas').cloneNode(true);
        topClone.setAttribute('id','topClone');
        const backClone=document.querySelector('#background').cloneNode(true);
        const background=document.querySelector('#background');
        background.innerHTML="";
        // background.appendChild(mainClone);//暂不考虑
        background.appendChild(topClone);
        background.appendChild(backClone);
        // main.innerHTML="";
        //这里应该接一个charticulator初始化
        document.querySelector('#topCanvas').innerHTML="";
        this.$store.commit('updateStaticDocument',{staticDocument:document.querySelector('#background')});
      }
      else{
        // document.querySelector('#mainCanvas').children[0].setAttribute("transform","translate(450,350)");
        // document.querySelector('#topCanvas').setAttribute("transform","scale(1.67)");
        // document.querySelector('#background').setAttribute("transform","scale(1.67)");
      }
    }
  },
  mounted(){
    const observe=new MutationObserver(
      ()=>{
        // scanAndGiveId(document.querySelector('#mainCanvas'))
        this.$store.commit('updateDoms',{doms:dfsCircles(document.querySelector('#mainCanvas'))})
        console.log('updateDom');
      }
    )
    observe.observe(document.querySelector('#mainCanvas'),{childList:true,subtree:true})
    
  },
  methods:{
    dMode(){
      this.$store.commit('clickEditing',{editing:false});
      document.querySelector('.documentTag').style.zIndex=2
      document.querySelector('.viewportTag').style.zIndex=0
    },
    vMode(){
      this.$store.commit('clickEditing',{editing:true});
      document.querySelector('.documentTag').style.zIndex=0
      document.querySelector('.viewportTag').style.zIndex=2

      this.$store.commit('changeLoadFile');
    },
    selectMore(e){
      const [dom]=e.path.filter(x=>['circle','foreignObject','text','rect','line'].indexOf(x.tagName)!==-1);
      if (this.$store.state.selectedDoms.indexOf(dom)===-1)
        this.$store.commit('updateSelectedDoms',{'selectedDoms':[...this.$store.state.selectedDoms,dom]})
      else{
        dom.style.outline='',
        this.$store.state.selectedDoms.splice(this.$store.state.selectedDoms.indexOf(dom),1);
      }
    },
    goodLine(){
      console.log('goodline')
      this.shiftLine=!this.shiftLine;
    },
    badLine(){
      console.log('badline')
      this.shiftLine=false;
    },
    scrollSvg(e){
      if (!this.editing){
        console.log(this.scrollTop)
        this.scrollTop=Math.max(0,(this.scrollTop+e.deltaY));
        this.svgHeightMax=Math.max(this.svgHeightMax,this.scrollTop+827)
        this.$store.commit('updateSvgHeightMax',{svgHeightMax:this.svgHeightMax});
        this.$store.commit('changeLoadFile');
      }
    },
    selectDom(e){
      const dom=e.path.filter(x=>['circle','foreignObject','text','rect','line'].indexOf(x.tagName)!==-1);
      this.$store.commit('updateSelectedDoms',{'selectedDoms':dom})
    },
    selectBeginEvent(e){
      if (this.addingRect){
        this.selectBegin=[e.offsetX,e.offsetY]
        this.selectEnd=[e.offsetX,e.offsetY]
        this.adding=true
      }
      else if (this.addingLine){
        this.lineBegin=[e.offsetX,e.offsetY]
        this.lineEnd=[e.offsetX,e.offsetY]
        // this.select=true;
      }
      else if (this.packing){
        this.packingArr=[{'x':e.offsetX,'y':e.offsetY}];
        this.packingPath=`M ${e.offsetX} ${e.offsetY}`;
        console.log('begin')
      }
      else{
        this.selectBegin=[e.offsetX,e.offsetY]
        this.selectEnd=[e.offsetX,e.offsetY]
        this.select=true;
      }
    },
    selectEndEvent(){
      if (this.addingRect){
        this.$store.commit('addingRect');
        const dom=document.querySelector('#addingRect').cloneNode()
        
        dom.removeAttribute('id')
        document.querySelector('#topCanvas').appendChild(dom)
        this.adding=false;
        // this.$store.commit('addingRect');
        // const dom=document.querySelector('#addingCircle').cloneNode()
        // const srcDom=document.querySelector('#addingRect')
        // dom.setAttribute('cx',this.selectBox.x+this.selectBox.width/2)
        // dom.setAttribute('cy',this.selectBox.y+this.selectBox.height/2)
        // dom.setAttribute('r',this.selectBox.width/2)
        // dom.style.fill=srcDom.style.fill;
        // dom.removeAttribute('id')
        // document.querySelector('#topCanvas').appendChild(dom)
        // this.adding=false;
      }
      if (this.addingLine){
        this.$store.commit('addingLine');
        
        const dom=document.querySelector('#addingLine').cloneNode()
        dom.removeAttribute('id')
        document.querySelector('#topCanvas').appendChild(dom)
        this.lineBegin=[]
        this.lineEnd=[]
      }
      if (this.packing){
        this.$store.commit('clickPacking');
        if (this.packingPath!=="")
          polyPacking(this.packingArr,this.$store.state.selectedDoms);
        this.packingPath="";
      }
      if (this.select){
        if (this.selectState==='normal'){
          const doms=dfsAllNodes()
          const selectedDoms=checkSelectedDoms(doms);
          if (selectedDoms){
            this.$store.commit('updateSelectedDoms',{selectedDoms});
          }
        }
        if (this.selectState==='addText'){
          this.$store.commit('updateKeyFrameEdited');
          const focusTextDom=addTextDom(document.querySelector('#topCanvas'),this.selectBox,this.editing);
          this.$store.state.IDManager.askNewID(focusTextDom,this.$store.state.focusTime);
          this.$store.commit('updateFocusTextDom',{focusTextDom});
          this.$store.commit('updateDoms',{doms:[...this.allDoms,focusTextDom]}) //更新一下全部dom的记录，从而支持选择和拖动
          this.$store.commit('addForeignObject',{newForeignObject:focusTextDom});
          d3.select(focusTextDom)
          .on('click',()=>this.$store.commit('updateFocusTextDom',{focusTextDom}));
          this.$store.commit('updateSelectState',{selectState:'normal'})//回归正常模式
          this.$store.commit('changeLoadFile');
        }
        this.select=false;
      } 
    },
    selectMoveEvent(e){
      if (this.lineBegin.length>0 && this.addingLine){
        if (this.shiftLine){
          if (Math.abs(e.offsetX-this.lineBegin[0])>Math.abs(e.offsetY-this.lineBegin[1])){
            this.lineEnd=[e.offsetX,this.lineBegin[1]]
          }
          else{
            this.lineEnd=[this.lineBegin[0],e.offsetY]
          }
        }
        else
          this.lineEnd=[e.offsetX,e.offsetY];
      }
      else if (this.packing && this.packingPath!==""){
        const lastPoint=this.packingArr[this.packingArr.length-1];
        this.packingArr.push({'x':lastPoint.x+e.movementX,'y':lastPoint.y+e.movementY});
        this.packingPath+=`l ${e.movementX} ${e.movementY}`;
      }
      else{
        if (!this.shiftLine)
          this.selectEnd=[this.selectEnd[0]+e.movementX,this.selectEnd[1]+e.movementY]
        else
          this.selectEnd=[this.selectEnd[0]+Math.max(e.movementX,e.movementY),this.selectEnd[1]+Math.max(e.movementX,e.movementY)]
      }
        
    },
    deleteDom(){
      this.selectedDoms.forEach(dom=>{
        dom.parentNode.removeChild(dom)
      })
    }
  }
}
</script>

<style lang="sass" scoped>
#svgdiv
  position: fixed
  width: 575px
  height: 369.64px
  left: 551px
  top: 267px
  overflow: hidden
  border: 1px dashed #000000
  border-radius: 10px

svg
  width: 575px
  height: 369.64px
  background: #FFFFFF
  box-sizing: border-box
  transition-property: all
  transition-duration: .3s
  overflow-y: scroll

.documentTag
  position: absolute
  top: 240px
  left: 501px

.viewportTag
  position: absolute
  top: 240px
  left: 595px

.editingCanvas
  // width: 575px
  // height: 369.64px
  // left: 551px
  // top: 267px
  
#selectBox
    stroke : gray
    stroke-dasharray : 4px
    stroke-opacity : 0.5
    fill : transparent



.notEditingCanvas
  // height: v-bind(svgHeightNonEditing)
  // width: 575px
  // height: 369.64px
  // left: 551px
  // top: 267px
  // width: 961px
  // left: 551px
  // top: 100px


</style>

<style>

.w-e-text-container{

  background-color: rgba(255,0,0,0) !important;
}

</style>
