<template>
  <div id='dataFilter'>
    <h2 class="el-title"><span class="el-text">DataFilter</span></h2>
    <el-button @click="selectFilter" style="transform: translate(50px, 0px);">Select</el-button>
    <div v-for="filter in filters" :key="`filter${filter}`">
      <label>{{filter.name}}</label>
      <br/>
      <input type='text' :name='filter.name' v-model="filter.value"/>
    </div>
  </div>
</template>

<script>

import {dfsCircles} from '../../../util/util'

export default {
  name:'dataFilter',
  computed:{
    initCharticulator(){
      return this.$store.state.initCharticulator;
    }
  },
  mounted(){
  const observe=new MutationObserver(
      ()=>{
        console.log('dataset',this.$store.state.charticulatorWrap.getDataSet());
        this.CharticulatorWrap=this.$store.state.charticulatorWrap;
        this.dataSetRow=this.CharticulatorWrap.getDataSet().rows;
        this.dataSetColumn=this.CharticulatorWrap.getDataSet().columns.map(x=>x.name);
        this.filters=[];
        this.dataSetColumn.forEach(column => {
          this.filters.push(
            {
              'name':column,
              'value':''
            }
          )});
      }
    )
    observe.observe(document.querySelector('#datasetView'),{childList:true,subtree:true})

  },
  watch:{
    initCharticulator(init){
      if (init){
        console.log(111,this.$store.state,this.$store.state.charticulatorWrap.getDataSet())
        this.CharticulatorWrap=this.$store.state.charticulatorWrap;
        this.dataSetRow=this.CharticulatorWrap.getDataSet().rows;
        this.dataSetColumn=this.CharticulatorWrap.getDataSet().columns.map(x=>x.name);
        this.dataSetColumn.forEach(column => {
          this.filters.push(
            {
              'name':column,
              'value':''
            }
          )});
      }
    }
  },
  data(){
    return {
      filters:[]
    }
  },
  methods:{
    selectFilter(){
      const beforeFilter=[];
      for (let i=0;i<this.dataSetRow.length;i+=1){
        beforeFilter.push(i)
      };
      const filteredAns=beforeFilter.filter((index)=>{
        let ans=true;
        this.filters.forEach(filter=>{
          if (filter.value!==''){
            console.log(this.dataSetRow[index][filter.name],filter.value)
            if (this.dataSetRow[index][filter.name]!==filter.value)
              ans=false;
          }
        })
        return ans;
      })
      const mainCanvas=document.querySelector('#mainCanvas')
      const dataDoms=Array.from(mainCanvas.childNodes[0].childNodes[1].childNodes[1].childNodes[0].childNodes).slice(1)//去掉第一个空的，后面都是glyph的不同组成部分。
      let selectedDoms=[];
      dataDoms.forEach((glyphDom,index)=>{
        const filteredDoms=filteredAns.map(index=>glyphDom.childNodes[index])
        //每行对应的取出来
        //目前没有考虑charticulator自己的聚合和过滤
        console.log(filteredDoms,index,'filter');
        filteredDoms.forEach(dom=>selectedDoms=[...selectedDoms,...dfsCircles(dom)])
        
      })
      console.log(111,selectedDoms)
      this.$store.commit('updateSelectedDoms',{selectedDoms});
    }
  }
}
</script>

<style lang="sass" scoped>
#dataFilter
  position: absolute
  width: 199px
  height: 446px
  left: 0px
  top: 536.58px
  background: white
  overflow: scroll
  border-top: solid 0.5px #888

span
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif
  font-weight: 700
  flex: 1 1
  margin-left: 35px

</style>