<template>
  <circle ref="circle" tabindex="0" :cx="position.x" :cy="position.y+5" r="5"
  @click.stop="clickCircle" @mousedown="dragBegin" 
  @mousemove="dragMove" @mouseup="dragEnd" @mouseleave="dragEnd"
  @contextmenu.prevent="rightClick"/>
</template>

<script>
export default {
  name:'miniNode',
  props:[
    'node'
  ],
  data(){
    return {
      dragging:false,
    }
  },
  mounted(){
  },
  computed:{
    screenHeightDividedBySvgHeigh(){
      return 827/this.$store.state.svgHeightMax; //minimap is 827 px;
    },
    type(){
      return this.node.type;
    },
    position(){
      return this.node.position;
    },
    dom(){
      return this.node.dom;
    }
  },
  methods:{
    clickCircle(){
      this.$store.commit('updateSelectedDoms',{selectedDoms:[this.dom]});
    },
    dragBegin(){
      this.dragging=true;
    },
    dragMove(e){
      if (this.dragging){
        const y=parseFloat(this.dom.getAttribute('y'))
        this.dom.setAttribute('y',y+e.movementY*this.$store.state.svgHeightMax/827)
        const cy=parseFloat(this.$refs.circle.getAttribute('cy'));
        this.$refs.circle.setAttribute('cy',cy+e.movementY);
      }
        this.$store.commit('dragMiniNode',{
          moveX:e.movementX,
          node:this.node,
        })
    },
    dragEnd(){
      this.dragging=false;
    }
  }
}
</script>

<style lang="sass" scoped>
circle
  fill: #F8B62D
</style>